import{utils}from'@ckb-lumos/base';import{getConfig as getSDKConfig}from'@ckb-lumos/config-manager';import{generateAddress}from'@ckb-lumos/helpers';function blake160(publicKey){return new utils.CKBHasher().update(publicKey).digestHex().slice(0,42);}export function getConfig(chainId){var config=getSDKConfig();if(chainId==='mainnet'&&config.PREFIX==='ckb'){return config;}if(chainId==='testnet'&&config.PREFIX==='ckt'){return config;}throw new Error('Invalid chainId');}export function scriptToAddress(script,{config=undefined}={}){return generateAddress(script,{config});}export function pubkeyToAddress(publicKey,{config}){var args=blake160(publicKey);var template=config.SCRIPTS.SECP256K1_BLAKE160;if(!template){throw new Error('SECP256K1_BLAKE160 not found in config');}var lockScript={codeHash:template.CODE_HASH,hashType:template.HASH_TYPE,args};return scriptToAddress(lockScript,{config});}